          <span>{{ (priceInfo.minPrice || priceInfo.actualPrice || 0)|formatPrice | formatPriceInt }}</span>
          <span class="float-number">{{ (priceInfo.minPrice || priceInfo.actualPrice || 0)|formatPrice | formatPriceFloat}}</span>
          <span class="tag" v-if="priceInfo.minPrice">起</span>
        </div>
      </div>
      <div class="bottom-price">
        <div class="original-price">
          ￥
          <span class="big">{{ (priceInfo.minOriginPrice || priceInfo.originPrice || 0 ) | formatPrice | formatPriceInt}}</span>
          <span>{{ (priceInfo.minOriginPrice || priceInfo.originPrice || 0)|formatPrice | formatPriceFloat}}</span>
          <span class="tag" v-if="priceInfo.minOriginPrice">起</span>
        </div>
        <div class="group-num">
          <span>{{activityInfo.clustersNum}}人团</span>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="activity-tips">距活动结束还剩:</div>
      <div class="countdown">
        <countdown :color="'#ED2856'" :activityType="activityInfo.activityType" :endTime="activityInfo.endTime" :startTime="activityInfo.startTime" />
      </div>
    </div>
  </div>
</template>
<script>
import countdown from './countdown'
export default {
  components: {
    countdown,
  },
  props: [
    'priceInfo',
    'activityInfo',
    'saleNum'
  ],
  filters: {
    formatSaleNumber(v) {
      const nv = Number(v);
      const nvn = (nv / 10000).toFixed(1) + '万'
      return nv <= 9999 ? nv : nvn
    },
    formatPriceInt(v) {
      return v.split('.')[0] + '.';
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    },
  },
}
</script>
